// Get Unique Brands from Json Data
import moment from "moment";
import React from "react";
import Client from "shopify-buy-with-tags";

export const getBrands = (products) => {
    var uniqueBrands = [];
    products.map((product, index) => {
        if (product.tags) {
            product.tags.map((tag) => {
                if (uniqueBrands.indexOf(tag) === -1) {
                    uniqueBrands.push(tag);
                }
            })
        }
    })
    return uniqueBrands;
}

const returnColorHex = (color) => {
    switch (color) {
        case "multi":
            break;
        case "tan":
            return "#f2e5c4";
        case "neon pink":
            return "#e8808c";
        case "denim":
            return "#3a488a";
        case "dark denim":
            return "#1f2542";
        case "hot pink":
            return "#fc5b99";
        case "bronze":
            return "#f5c70f";
        case "rose gold":
            return "#b76e79";
        case "bronze":
            return "#f5c70f";
        case "bronze":
            return "#f5c70f";
        case "bronze":
            return "#f5c70f";
        default:
            return color;
    }
}

