const fixDataResponseIssues = (response) => {
    const fixedResponse = []
    for (let i=0; i < response.length; i++) {
        try {
            for (let a=0; a < response[i].options.length; a++) {
                if (response[i].options[a].name === "Size") {
                    const values = response[i]
                    for (let b=0; b < response[i].options[a].values.length; b++) {
                        const value = response[i].options[a].values[b].value
                        if (value.includes("/")) {
                            const temp = this.state.sizingProblems;
                            const caseCheck = value.toLowerCase().trim()
                            if (caseCheck.includes("small")) {
                                const newError = {
                                    errorLocated: "Sizing Error Detected at Index " + i + " On Item : " + response[i].title,
                                    fixed: "updated " + response[i].options[a].values[b].value + " to " + "Small"
                                }
                                temp.push(newError)
                                values.options[a].values[b].value = "Small";

                            }
                            if (caseCheck.includes("large")) {
                                const newCall = {
                                    errorLocated: "Sizing Error Detected at Index " + i + " On Item : " + response[i].title,
                                    fixed: "updated " + response[i].options[a].values[b].value + " to " + "Large"
                                }
                                temp.push(newCall)
                                values.options[a].values[b].value = "Large";
                            }
                            const split = caseCheck.split("/")
                            if (split.length > 1) {
                                const parse = parseInt(split[0])
                                if (parse) {
                                    const newCall = {
                                        errorLocated: "Sizing Error Detected at Index " + i + " On Item : " + response[i].title,
                                        fixed: "updated " + response[i].options[a].values[b].value + " to " + split[0]
                                    }
                                    temp.push(newCall)
                                    values.options[a].values[b].value = split[0];
                                }
                            }

                            this.setState({sizingProblems: temp})

                        }
                    }
                    //this app depends on the variant category as shopify puts all their prices and checkout w/item information in variants
                    try {
                        if (!response[i].variants[0]) {
                            console.log("malformed product skipped :: ")
                            console.log(response[i])
                            continue;
                        }

                    }
                    catch {
                        continue;
                    }

                    if (values.length > 0) {
                        fixedResponse.push(values);
                    }
                    else {
                        fixedResponse.push(response[i])
                    }

                }
            }
        }
        catch {
            console.log("Error will happen at")
            //console.log(response[i])
        }

    }
    return fixedResponse;
}
