import React, { Component } from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import {connect} from 'react-redux'

import {getUnderFifteen, getWomensWear, getVisibleproducts, getCollection} from '../../../services/index'
import {addToCart, addToWishlist, addToCompare} from "../../../actions/index";
import ProductItem from './product-item';
import { Col, Row, Container } from "react-bootstrap";

import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import Client from "shopify-buy-with-tags";


const responsive = {
    superLargeDesktop: {
        breakpoint: { max: 4000, min: 3000 },
        items: 8
    },
    desktop: {
        breakpoint: { max: 3000, min: 2048 },
        items: 7
    },
    smallDesktop: {
      breakpoint: { max: 2048, min: 1024},
      items: 4
    },
    tablet: {
        breakpoint: {max: 1024, min: 767},
        items: 3
    },
    smallTablet: {
        breakpoint: { max: 767, min: 464 },
        items: 2
    },
    mobile: {
        breakpoint: { max: 464, min: 0 },
        items: 2
    }
};
const bestSellerID = "Z2lkOi8vc2hvcGlmeS9Db2xsZWN0aW9uLzIxNTkyMzQ5MDk3Mw=="
const winterID = "Z2lkOi8vc2hvcGlmeS9Db2xsZWN0aW9uLzIyODMzMTUyMDE1Nw==";
const trendingID = "Z2lkOi8vc2hvcGlmeS9Db2xsZWN0aW9uLzIyODg2MjIzMDY4NQ==";

class SpecialProducts extends Component {
    constructor(props) {
        super(props);
        this.state = {
            update: false,
            bestProductSellers: [],
            womensProductSellers:[],
            under15Sellers: [],
        }
    }

    componentDidMount() {
        this.renderBestSellers("best");
        this.renderBestSellers("womens");
        this.renderBestSellers("under15")
    }

    renderBestSellers = async (type) => {
        switch (type) {
            case "best":
                getCollection(bestSellerID)
                    .then((products) => {
                        if (products) {
                            console.log(products)
                            this.setState({bestProductSellers: products})
                        }
                    })
                break;
            case "womens":
                getCollection(winterID)
                    .then((products) => {
                        if (products) {
                            this.setState({womensProductSellers: products})
                        }
                    })
                break;
            case "under15":
                getCollection(trendingID)
                    .then((products) => {
                        if (products) {
                            this.setState({under15Sellers: products})
                        }
                    })
                break;
        }
    }


    render (){

        const {underFifteen,womensWear, symbol, addToCart, addToWishlist, addToCompare} = this.props
        return (
            <div>
                <section className="section-b-space mt-3">
                    <Container fluid>
                        <div style={{marginTop:55}}>
                            <div style={{marginBottom:55}}>
                                <h4 className={"specialProductsTitle my-4"} style={{textAlign:"center", marginLeft:5, marginRight:5, fontSize:"calc(1.5vw + 1.5vh + 2vmin)", position:"relative"}}>
                                    <span style={{ position:"relative"}}>
                                        <span>
                                            <span style={{backgroundColor:"#90EE90",color:"black",  paddingLeft:35, paddingRight:35}}>Trending Now</span>
                                        </span>
                                    </span>
                                </h4>
                            </div>
                            {
                                this.state.bestProductSellers.length ?
                                    <Carousel responsive={responsive}>

                                        {
                                            this.state.bestProductSellers.map((product, index ) =>
                                                <div key={index} className={"mx-2"}>
                                                    <ProductItem product={product} symbol={this.props.symbol}
                                                                 onAddToCompareClicked={() => addToCompare(product)}
                                                                 onAddToWishlistClicked={() => addToWishlist(product)}
                                                                 onAddToCartClicked={(item, quantity) => addToCart(item, quantity, this.props.client)}
                                                                 window={this.props.window} special={true}
                                                    />
                                                </div>)
                                        }
                                    </Carousel>:null
                            }
                        </div>
                        <div style={{marginBottom:55}}>
                            <h4 className={"specialProductsTitle my-4"} style={{textAlign:"center", marginLeft:5, marginRight:5, fontSize:"calc(1.5vw + 1.5vh + 2vmin)", position:"relative"}}>
                                    <span style={{ position:"relative"}}>
                                        <span>
                                            <span style={{backgroundColor:"#90EE90",color:"black",  paddingLeft:35, paddingRight:35}}>Winter Collection</span>
                                        </span>
                                    </span>
                            </h4>
                            {
                                this.state.womensProductSellers.length ?
                                    <Carousel responsive={responsive}>
                                        {
                                            this.state.womensProductSellers.map((product, index ) =>
                                                <div key={index} className={"mx-2"}>
                                                    <ProductItem product={product} symbol={this.props.symbol}
                                                                 onAddToCompareClicked={() => addToCompare(product)}
                                                                 onAddToWishlistClicked={() => addToWishlist(product)}
                                                                 onAddToCartClicked={(item, quantity) => addToCart(item, quantity, this.props.client)}
                                                                 window={this.props.window} special={true}
                                                    />
                                                </div>)
                                        }
                                    </Carousel>:null
                            }



                        </div>

                        <div style={{marginTop:55}}>
                            <div style={{marginBottom:55}}>
                                <h4 className={"specialProductsTitle my-4"} style={{textAlign:"center", marginLeft:5, marginRight:5, fontSize:"calc(1.5vw + 1.5vh + 2vmin)", position:"relative"}}>
                                    <span style={{ position:"relative"}}>
                                        <span>
                                            <span style={{backgroundColor:"#90EE90",color:"black",  paddingLeft:35, paddingRight:35}}>Recently Purchased</span>
                                        </span>
                                    </span>
                                </h4>
                            </div>
                            {
                                this.state.under15Sellers.length ?
                                    <Carousel responsive={responsive}>
                                        {
                                            this.state.under15Sellers.map((product, index ) =>
                                                <div key={index} className={"mx-2"}>
                                                    <ProductItem product={product} symbol={this.props.symbol}
                                                                 onAddToCompareClicked={() => addToCompare(product)}
                                                                 onAddToWishlistClicked={() => addToWishlist(product)}
                                                                 onAddToCartClicked={(item, quantity) => addToCart(item, quantity, this.props.client)}
                                                                 window={this.props.window} special={true}
                                                    />
                                                </div>)
                                        }
                                    </Carousel>:null
                            }
                        </div>
                    </Container>
                </section>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    underFifteen: getUnderFifteen,
    womensWear: getWomensWear,
    symbol: state.data.symbol,
    client: state.client,
    window: state.window.window
})

export default connect(mapStateToProps, {addToCart, addToWishlist, addToCompare, getVisibleproducts }) (SpecialProducts);
